.table {
  width: 100%;
  font-size: 1.2rem;
  color: #757575;
  text-align: left; }
  .table tr {
    height: 48px;
    line-height: 48px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.12); }
  .table th {
    font-weight: 700; }
    .table th:first-letter {
      text-transform: capitalize; }
  .table th, .table td {
    position: relative;
    padding: 0 1.8rem; }
    .table th.selectable, .table td.selectable {
      width: 1.8rem;
      padding-right: 0; }
      .table th.selectable > *, .table td.selectable > * {
        margin: 0; }

.row {
  transition: background-color 0.35s cubic-bezier(0.4, 0, 0.2, 1); }
  .row:last-child {
    border-color: transparent; }
  .row > td > input {
    display: block;
    width: 100%;
    background-color: transparent;
    border: 0; }

.selected, .row:hover {
  background-color: #eee; }

.editable > * {
  cursor: pointer; }



