.enter, .leave {
  position: absolute; }

.enterActive, .leaveActive {
  transition: transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 500ms; }

.enter {
  opacity: 0;
  transform: scale(0.85); }
  .enter.enterActive {
    opacity: 1;
    transform: scale(1); }

.leave {
  opacity: 1;
  transform: scale(1); }
  .leave.leaveActive {
    opacity: 0;
    transform: scale(1.25); }



